
.not-found-page {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000000;
  }
  
  .not-found-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 14px;
    text-align: center;
    width: 81%;
    margin: auto;
  }
  
  .not-found-image {
    width: 100%;
    max-width: 354px;
  }
  
  .not-found-heading {
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 550;
    margin: 0;
    margin-top: 16px;
    color: #f8fafc;
  }
  
  .not-found-description {
    font-family: 'Roboto';
    font-size: 15px;
    line-height: 1.3;
    font-weight: 350;
    margin: 0;
    color: #f1f5f9;
  }
  
  @media screen and (min-width: 768px) {
    .not-found-image {
      max-width: 415px;
    }
  
    .not-found-heading {
      font-size: 25px;
    }
  }
  
  @media screen and (min-width: 1200px) {
    .not-found-heading {
      font-size: 34px;
    }
  }
  